/* --- "Elegant Reveal" Welcome Screen --- */
.simple-welcome-screen {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top:0; left:0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  background-color: hsl(var(--background)); /* Clean, static background from theme */
  color: hsl(var(--foreground));
  cursor: pointer; /* Indicate it's clickable to dismiss */
}

.simple-welcome-tagline { /* Styles for the AnimatedTagline component's container */
  text-align: center;
  font-weight: 300; /* Poppins Light */
  letter-spacing: 0.025em;
  font-size: clamp(1.1rem, 3vw, 1.5rem); /* Adjusted for prominence */
  color: hsl(var(--foreground), 0.9);
}
.dark .simple-welcome-tagline {
  color: hsl(var(--foreground), 0.85);
}

/* Styles applied to the logo elements specifically within the simple-welcome-screen */
.simple-welcome-screen .welcome-logo-container .logo-icon-container {
  width: clamp(var(--heart-icon-splash-size-min), var(--heart-icon-splash-size-vw), var(--heart-icon-splash-size-max));
  height: clamp(var(--heart-icon-splash-size-min), var(--heart-icon-splash-size-vw), var(--heart-icon-splash-size-max));
}

.simple-welcome-screen .welcome-logo-container .logo-icon-container svg {
    width: 60%;
    height: 60%;
}

.simple-welcome-screen .welcome-logo-container .logo-text {
  font-size: clamp(var(--logo-text-splash-size-min), var(--logo-text-splash-size-vw), var(--logo-text-splash-size-max));
  /* Any other text specific styles for the welcome screen logo text if needed */
}
/* --- End of "Elegant Reveal" Welcome Screen Styles --- */


/* --- "MediAssistant Spark" Welcome Screen Styles --- */
@keyframes spark-background-animation-light {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}
@keyframes spark-background-animation-dark {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

.spark-welcome-screen {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0; left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  color: hsl(var(--foreground)); /* Default text color */
  cursor: pointer;

  background: linear-gradient(
    135deg,
    hsl(var(--spark-bg-start-light-h), var(--spark-bg-start-light-s), var(--spark-bg-start-light-l)),
    hsl(var(--spark-bg-mid-light-h), var(--spark-bg-mid-light-s), var(--spark-bg-mid-light-l)),
    hsl(var(--spark-bg-end-light-h), var(--spark-bg-end-light-s), var(--spark-bg-end-light-l))
  );
  background-size: 400% 400%;
  animation: spark-background-animation-light 20s ease infinite;
}

.dark .spark-welcome-screen {
  background: linear-gradient(
    135deg,
    hsl(var(--spark-bg-start-dark-h), var(--spark-bg-start-dark-s), var(--spark-bg-start-dark-l)),
    hsl(var(--spark-bg-mid-dark-h), var(--spark-bg-mid-dark-s), var(--spark-bg-mid-dark-l)),
    hsl(var(--spark-bg-end-dark-h), var(--spark-bg-end-dark-s), var(--spark-bg-end-dark-l))
  );
  background-size: 400% 400%;
  animation-name: spark-background-animation-dark; /* Use dark mode animation */
}

/* Styles for individual spark particles (used by WelcomeDisplay.tsx) */
.spark-particle {
  width: 5px;
  height: 5px;
  border-radius: 50%;
  position: absolute; /* Positioned by framer-motion */
  box-shadow: 0 0 8px 2px currentColor; /* Creates a glow effect using the particle's color */
  opacity: 0; /* Initial state for framer-motion */
}
