.bg-\[hsl\(var\(--spark-particle-color-1\)\)\] { background-color: hsl(var(--spark-particle-color-1-h), var(--spark-particle-color-1-s), var(--spark-particle-color-1-l)); color: hsl(var(--spark-particle-color-1-h), var(--spark-particle-color-1-s), var(--spark-particle-color-1-l));}
.bg-\[hsl\(var\(--spark-particle-color-2\)\)\] { background-color: hsl(var(--spark-particle-color-2-h), var(--spark-particle-color-2-s), var(--spark-particle-color-2-l)); color: hsl(var(--spark-particle-color-2-h), var(--spark-particle-color-2-s), var(--spark-particle-color-2-l));}
.bg-\[hsl\(var\(--spark-particle-color-3\)\)\] { background-color: hsl(var(--spark-particle-color-3-h), var(--spark-particle-color-3-s), var(--spark-particle-color-3-l)); color: hsl(var(--spark-particle-color-3-h), var(--spark-particle-color-3-s), var(--spark-particle-color-3-l));}
.bg-\[hsl\(var\(--spark-particle-color-4\)\)\] { background-color: hsl(var(--spark-particle-color-4-h), var(--spark-particle-color-4-s), var(--spark-particle-color-4-l)); color: hsl(var(--spark-particle-color-4-h), var(--spark-particle-color-4-s), var(--spark-particle-color-4-l));}
